import React, { useEffect, useState } from "react";
import Notice from "../../types/Notice";
import NoticeService from "../../services/NoticeService";

import { Pagination } from "@mui/material";
import { Link } from "react-router-dom";

function NoticeList(props:any) {
  //  
  const [notice, setNotice] = useState<Array<Notice>>([]);

  const [searchUname, setSearchUname] = useState<string>("");

  // todo   : page( ), count(  ), pageSize(3,6,9 )
  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(1);
  const [pageSize, setPageSize] = useState<number>(12); //   
  // todo  pageSizes :  (selectbox )
  const pageSizes = [3, 6, 9, 12];

  //  
  useEffect(() => {
    retrieveNotice();
  }, [page, pageSize]);

  // todo)   
  const retrieveNotice = () => {
    //    : (page), 1 (pageSize)
    NoticeService.getAll(searchUname, page - 1, pageSize) //  
      .then((response: any) => {
        //   
        // es6(js)  :   
        //  
        // const dept =  response.data.dept;  // 
        // const totalPage = response.data.totalPages;  // 
        const { notice, totalPages } = response.data;
        // dept 
        setNotice(notice);
        setCount(totalPages);
        //  
        console.log("response", response.data);
      })
      .catch((e: Error) => {
        //   
        console.log(e);
      });
  };

  //    
  const onChangeSearchUname = (e: any) => {
    const searchUname = e.target.value;
    setSearchUname(searchUname);
  };

  // todo handlePageSizeChange() : pageSize    
  // select   :     
  const handlePageSizeChange = (e: any) => {
    setPageSize(e.target.value); // 1   (3, 6, 9)
    setPage(1); //   : 1 
  };

  // todo Pagination ()
  // page   => page   
  const handlePageChange = (e: any, value: number) => {
    // value ==  
    setPage(value);
  };

  return (
    <>
      <div className="d-grid gap-2 d-md-flex justify-content-md-end">
        <Link to="#">
          <button type="button" className="btn btn-outline-dark mb-3 "  onClick={() => props.handleChangeNotice("addNotice")}>
            
          </button>
        </Link>
      </div>

      {/* table start( ) */}
      <div className="col-md-12">
        {/* table start */}
        <table className="table">
          <thead className="notice-table text-white" style={{backgroundColor: "#bd5d38"}}>
            <tr>
              <th className="text-center col-md-1" scope="col">
                
              </th>
              <th className="text-center col-md-4" scope="col">
                
              </th>
              <th className="text-center col-md-1" scope="col">
                
              </th>
              <th className="text-center col-md-3" scope="col">
                
              </th>
            </tr>
          </thead>
          <tbody>
            {notice &&
              notice.map((data) => (
                <tr key={data.id}>
                  <td className="text-center">{data.id}</td>
                  <td className="text-center" >
                    <Link to="#"><span onClick={()=>props.handleChangeNotice("noticeDetail", data.id)}>{data.title}</span></Link>
                  </td>
                  <td className="text-center">{data.userName}</td>
                  <td className="text-center">{data.insertTime}</td>
                </tr>
              ))}
          </tbody>
        </table>
        {/* table end */}
      </div>
      {/* table end */}
      {/* dname start   */}
      <div className="row mb-5 justify-content-center">
        {/* w-50 :  , mx-auto : (margin: 0 auto), justify-content-center */}
        <div className="col-12 w-50 input-group mb-3">
          <input
            type="text"
            className="form-control"
            placeholder=" "
            value={searchUname}
            onChange={onChangeSearchUname}
          />
          <div className="input-group-append">
            <button
              className="btn btn-outline-secondary"
              type="button"
              onClick={retrieveNotice}
            >
              Search
            </button>
          </div>
        </div>
      </div>
      {/* dname end */}

      {/* paging (  )*/}
      <div className="mt-3  ">
        {"  : "}
        {/*  size  selectbox  */}
        <select onChange={handlePageSizeChange} value={pageSize}>
          {pageSizes.map((size) => (
            <option key={size} value={size}>
              {size + ""}
            </option>
          ))}
        </select>
        {/*  size  selectbox  */}

        {/* Material UI()   */}
        {/* count :   (), page :    */}
        {/* count={1}, page={} */}

        <Pagination
          className="pagination"
          count={count}
          page={page}
          siblingCount={1}
          boundaryCount={1}
          variant="outlined"
          shape="rounded"
          onChange={handlePageChange}
          color="primary"
        />

        {/* Material UI()   */}
      </div>
      {/* paging  */}
    </>
  );
}

export default NoticeList;
