import React, { useState } from "react";
import IReplyBoard from "../../types/IReplyBoard";
import ReplyBoardService from "../../services/ReplyBoardService";

function AddReplyBoard() {
  // todo:  
  // todo:  
  const initialReplyBoard = {
    bid: null,
    boardTitle: "",
    boardContent: "",
    boardWriter: "",
    viewCnt: 0,
    boardGroup: null,
    boardParent: 0,
  };

  // replyBoard 
  const [replyBoard, setReplyBoard] = useState<IReplyBoard>(initialReplyBoard);
  //   submitted = true 
  const [submitted, setSubmitted] = useState<boolean>(false);

  // todo:  
  // input   
  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target; // 
    setReplyBoard({ ...replyBoard, [name]: value }); // 
  };

  //  
  const saveReplyBoard = () => {
    //  
    var data = {
      boardTitle: replyBoard.boardTitle,
      boardContent: replyBoard.boardContent,
      boardWriter: replyBoard.boardWriter,
      viewCnt: replyBoard.viewCnt,
      boardGroup: null, //  
      boardParent: 0, //  
    };

    ReplyBoardService.createBoard(data) //   
      .then((response: any) => {
        
        setSubmitted(true);
        console.log(response.data);
        alert(".");
        window.location.reload();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  //    :   ->   ( )
  const newReplyBoard = () => {
    setReplyBoard(initialReplyBoard); // replyBoard 
    setSubmitted(false); // submitted  
  };

  return (
    // 
    <div className="row">
      {submitted ? (
        <div className="col-6 mx-auto"></div>
      ) : (
        <>
          <h4> </h4>

          <div className="col-6 mx-auto mt-5">
            <div className="row g-3 align-items-center mb-3">
              <div className="col-3">
                <label htmlFor="boardTitle" className="col-form-label">
                  
                </label>
              </div>

              <div className="col-9">
                <input
                  type="text"
                  id="boardTitle"
                  required
                  className="form-control"
                  value={replyBoard.boardTitle}
                  onChange={handleInputChange}
                  placeholder=" "
                  name="boardTitle"
                />
              </div>
            </div>

            <div className="row g-3 align-items-center mb-3">
              <div className="col-3">
                <label htmlFor="boardContent" className="col-form-label">
                  
                </label>
              </div>

              <div className="col-9">
                <input
                  type="text"
                  id="boardContent"
                  required
                  className="form-control"
                  value={replyBoard.boardContent}
                  onChange={handleInputChange}
                  placeholder=" "
                  name="boardContent"
                />
              </div>
            </div>

            <div className="row g-3 align-items-center mb-3">
              <div className="col-3">
                <label htmlFor="boardWriter" className="col-form-label">
                  
                </label>
              </div>

              <div className="col-9">
                <input
                  type="text"
                  id="boardWriter"
                  required
                  className="form-control"
                  value={replyBoard.boardWriter}
                  onChange={handleInputChange}
                  placeholder="  "
                  name="boardWriter"
                />
              </div>
            </div>

            <div className="row g-3 mt-3 mb-3">
              <button
                onClick={saveReplyBoard}
                className="btn btn-outline-warning ms-2 col"
              >
                
              </button>
            </div>
          </div>
        </>
      )}
    </div>
  );
}

export default AddReplyBoard;
