import React, { useEffect, useState } from "react";
import IReplyBoard from "../../types/IReplyBoard";
import ReplyBoardService from "../../services/ReplyBoardService";
import { Pagination } from "@mui/material";
import { Link } from "react-router-dom";

function ReplyBoardList(props: any) {
  // todo ) 
  //  (+)  
  //  1  
  const [replyBoard, setReplyBoard] = useState<Array<IReplyBoard>>([]);

  //  
  const [searchBoardTitle, setSearchBoardTitle] = useState<string>("");

  // todo   : page( ), count(  ), pageSize(3,6,9 )
  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(1);
  const [pageSize, setPageSize] = useState<number>(9); //   
  // todo  pageSizes :  (selectbox )
  const pageSizes = [3, 6, 9];

  // todo ) 

  //  1)  mounted    : useEffect(() => {},[])
  //  2)      : useEffect(() => {},[ ])
  useEffect(() => {
    retrieveReplyBoard(); //  
  }, [page, pageSize]);

  //   
  const retrieveReplyBoard = () => {
    //    : (page), 1 (pageSize)
    ReplyBoardService.getAll(searchBoardTitle, page - 1, pageSize) //  
      .then((response: any) => {
        //   
        // es6(js)  :   
        //  
        // const dept =  response.data.dept;  // 
        // const totalPage = response.data.totalPages;  // 
        const { replyBoard, totalPages } = response.data;
        // dept 
        setReplyBoard(replyBoard);
        setCount(totalPages);
        //  
        console.log("response", response.data);
      })
      .catch((e: Error) => {
        //   
        console.log(e);
      });
  };

  //    
  const onChangeSearchBoardTitle = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchBoardTitle(e.target.value);
  };

  // todo handlePageSizeChange() : pageSize    
  // select   :     
  const handlePageSizeChange = (e: any) => {
    setPageSize(e.target.value); // 1   (3, 6, 9)
    setPage(1); //   : 1 
  };

  // todo Pagination ()
  // page   => page   
  const handlePageChange = (e: any, value: number) => {
    // value ==  
    setPage(value);
  };

  // -------------------------------------------------------

  // todo ::   
  // 1) reply  
  const initialReply = {
    bid: null,
    boardTitle: "",
    boardContent: "",
    boardWriter: "",
    viewCnt: 0,
    boardGroup: null,
    boardParent: 0,
  };

  // 2)     
  const [reply, setReply] = useState(initialReply);
  // 3) reply      : true/false
  const [replyClicked, setReplyClicked] = useState(false);

  // todo ::   
  // 1) input   
  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target; // 
    setReply({ ...reply, [name]: value }); // 
  };

  // 2)   (insert)
  const saveReply = () => {
    //  
    let data = {
      boardTitle: reply.boardTitle,
      boardContent: reply.boardContent,
      boardWriter: reply.boardWriter,
      viewCnt: 0,
      // ( == )
      // rule : 1)      0 
      //        2)     (bid) 
      boardGroup: reply.bid,
      // 
      // rule : 1)        (bid) 
      //        2)    (bid) 
      boardParent: reply.bid,
    };

    ReplyBoardService.create(data) //    
      .then((response: any) => {
        setReplyClicked(false);
        alert(" .");
        //   
        retrieveReplyBoard();
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // 3)  reply       
  const newReply = (data: any) => {
    //  ()  : boardContent: ""  
    setReply({ ...data, boardContent: "" });
    //     : replyClicked : true
    setReplyClicked(true);
  };

  // 4)   
  const closeReply = () => {
    //   
    setReplyClicked(false);
  };

  return (
    <div>
      <div className="d-grid gap-2 d-md-flex justify-content-md-end">
        <Link to="#">
          <button type="button" className="btn btn-outline-dark mb-3 "  onClick={() => props.handleChangeReplyBoard("addReplyBoard")}>
            
          </button>
        </Link>
      </div>
      {/* (1) + (2) */}
      <div className="col-md-12">
        {/* table start() */}
        <table className="table">
          <thead
            className="replyBoard-table text-white"
            style={{ backgroundColor: "#bd5d38" }}
          >
            <tr className="text-center">
              <th scope="col">No</th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col">Actions</th>
            </tr>
          </thead>
          <tbody>
            {replyBoard &&
              replyBoard.map((data, index) => (
                //    react    :    rerending   
                <tr key={index}>
                  <td className="text-center">{data.bid}</td>
                  <td>{data.boardTitle}</td>
                  <td >{data.boardContent}</td>
                  <td className="text-center"><b>{data.boardWriter}</b></td>

                  <td className="text-center">
                    {/*      */}
                    {data.boardParent == 0 && (
                      <Link to={"#"}>
                        {/*  : onClick={} :    */}
                        {/*  : onClick={()=> ()} :    */}
                        <span
                          className="badge bg-warning"
                          onClick={() => newReply(data)}
                        >
                          Reply
                        </span>
                      </Link>
                    )}
                  </td>
                  <td className="text-center">
                    {/*    */}
                    <Link to="#">
                      <span
                        className="badge bg-success"
                        onClick={() =>
                          props.handleChangeReplyBoard(
                            "replyBoardDetail",
                            data.bid,
                            data.boardParent
                          )
                        }
                      >
                        Edit
                      </span>
                    </Link>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
        {/* table end */}

        {/* reply form start ()*/}
        <div>
          {/*  &&  :  = true   */}
          {/*  &&  :  = false   */}
          {replyClicked && (
            <div className="col-md-12 row mb-5">
              <br/>
              <hr/>
              <h4> </h4>
              <div className="col-md-12 row mt-2">
                <label
                  htmlFor="boardContent"
                  className="col-md-2 col-form-label"
                >
                  
                </label>
                <div className="col-md-10">
                  <input
                    type="text"
                    className="form-control"
                    id="boardContent"
                    required
                    value={reply.boardContent}
                    onChange={handleInputChange}
                    name="boardContent"
                  />
                </div>
              </div>

              <div className="col-md-12 row mt-2">
                <label
                  htmlFor="boardWriter"
                  className="col-md-2 col-form-label"
                >
                   
                </label>
                <div className="col-md-10">
                  <input
                    type="text"
                    className="form-control"
                    id="boardWriter"
                    required
                    value={reply.boardWriter}
                    onChange={handleInputChange}
                    name="boardWriter"
                  />
                </div>
              </div>

              <div className="row px-4 mt-2">
                <button
                  onClick={saveReply}
                  className="btn btn-success mt-3 col-md-5"
                >
                  
                </button>
                <div className="col-md-2"></div>

                <button
                  onClick={closeReply}
                  className="btn btn-danger mt-3 col-md-5"
                >
                  
                </button>
              </div>
            </div>
          )}
        </div>
        {/* reply form end */}
        {/* search start ( )*/}
        <div className="row mb-5 justify-content-center">
          <div className="col-12 w-50 input-group mb-3">
            <input
              type="text"
              className="form-control"
              placeholder="Search by title"
              value={searchBoardTitle}
              onChange={onChangeSearchBoardTitle}
            />
            <button
              className="btn btn-outline-secondary"
              type="button"
              onClick={retrieveReplyBoard}
            >
              Search
            </button>
          </div>
        </div>
        {/* search end */}

        {/* paging (  )*/}
        <div className="mt-3  ">
          {"  : "}
          {/*  size  selectbox  */}
          <select onChange={handlePageSizeChange} value={pageSize}>
            {pageSizes.map((size) => (
              <option key={size} value={size}>
                {size + ""}
              </option>
            ))}
          </select>
          {/*  size  selectbox  */}

          {/* Material UI()   */}
          {/* count :   (), page :    */}
          {/* count={1}, page={} */}

          <Pagination
            className="pagination"
            count={count}
            page={page}
            siblingCount={1}
            boundaryCount={1}
            variant="outlined"
            shape="rounded"
            onChange={handlePageChange}
            color="primary"
          />

          {/* Material UI()   */}
        </div>
        {/* paging  */}
      </div>
    </div>
  );
}

export default ReplyBoardList;
