import React, { useEffect, useState } from "react";
import { Pagination } from "@mui/material";
import { Link } from "react-router-dom";
import IFileDb from "../../types/advanced/IFileDb";
import FileDbService from "../../services/advanced/FileDbService";

function FileDbList(props:any) {
  // todo : 
  //   
  const [fileDb, setFileDb] = useState<Array<IFileDb>>([]);
  // title  
  const [searchTitle, setSearchTitle] = useState<string>("");
  // todo :  /    
  const [message, setMessage] = useState<string>("");

  // todo:   : page(), count(), pageSize(3,6,9 )
  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(1);
  const [pageSize, setPageSize] = useState<number>(3); // 1
  // todo:  pageSizes :  (  )
  const pageSizes = [3, 6, 9];

  // todo : 
  // 1)  mounted    : useEffect(() => {},[])
  // 2)     : useEffect(() => {},[])
  useEffect(() => {
    retrieveFileDb(); //  
  }, [page, pageSize]);

  //    
  const retrieveFileDb = () => {
    //    : + (page), 1(pageSize)
    FileDbService.getFiles(searchTitle, page - 1, pageSize) //  
      .then((response: any) => {
        const { fileDb, totalPages } = response.data;
        setFileDb(fileDb);
        setCount(totalPages);
        console.log("response", response.data);
      })
      .catch((e: Error) => {
        //   
        console.log(e);
      });
  };

  //     
  const onChangeSearchTitle = (e: any) => {
    setSearchTitle(e.target.value);
  };

  // (uuid)
  const deleteImage = (uuid:any) => {
    FileDbService.deleteFile(uuid) //  
      .then((response: any) => {
        console.log(response.data);
        setMessage(".");
        // 
        retrieveFileDb();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // todo: handlePageSizeChange() : pageSize    
  //  select    :  ->  
  const handlePageSizeChange = (event: any) => {
    setPageSize(event.target.value); // 1 (3,6,9)
    setPage(1); //  : 1 
  };

  //  todo: Pagination  ()
  //     => page   
  const handlePageChange = (event: any, value: number) => {
    // value ==  
    setPage(value);
  };
  return (
    <>
    <div className="d-grid gap-2 d-md-flex justify-content-md-end">
        <Link to="#">
          <button type="button" className="btn btn-outline-dark mb-3 " onClick={() => props.handleChangeImgBoard("addImgBoard")}>
             
          </button>
        </Link>
      </div>

      <div className="col-md-8">
        {/*  start */}
        <div className="input-group mb-3">
          <input
            type="text"
            className="form-control"
            placeholder="Search by title"
            value={searchTitle}
            onChange={onChangeSearchTitle}
          />
          <div className="input-group-append">
            <button
              className="btn btn-outline-secondary"
              type="button"
              onClick={retrieveFileDb}
            >
              Search
            </button>
          </div>
        </div>
        {/*  end */}
      </div>

      <div className="col-md-12 mt-3">
        <h4>FileDb List</h4>
        {/* page start */}
        <div className="mt-3">
          {"Items per Page: "}
          <select onChange={handlePageSizeChange} value={pageSize}>
            {pageSizes.map((size) => (
              <option key={size} value={size}>
                {size}
              </option>
            ))}
          </select>

          <Pagination
            className="my-3 pagination"
            count={count}
            page={page}
            siblingCount={1}
            boundaryCount={1}
            variant="outlined"
            shape="rounded"
            onChange={handlePageChange}
          />
        </div>
        {/* page end */}

        {/* upload /    */}
        {message && (
          <div className="alert alert-success" role="alert">
            {message}
          </div>
        )}
        {/* upload /    */}

        {/*   start */}
        <div className="row">
          {fileDb &&
            fileDb.map((data, index) => (
              <div className="col-sm-6" key={index}>
                <div className="card">
                  <img src={data.fileUrl} className="card-img-top" alt="" />
                  <div className="card-body">
                    <h5 className="card-title">{data.fileTitle}</h5>
                    <p className="card-text">{data.fileContent}</p>
                    <Link to="#">
                      <span className="badge bg-warning"
                      onClick={() => props.handleChangeImgBoard("imgBoardDetail", data.uuid)}
                      >Edit</span>
                    </Link>
                    <a
                      style={{ color: "inherit" }}
                      className="ms-2"
                      onClick={() => {
                        deleteImage(data.uuid);
                      }}
                    >
                      <span className="badge bg-danger">Delete</span>
                    </a>
                  </div>
                </div>
              </div>
            ))}
        </div>
        {/*   end */}
      </div>
    </>
  );
}

export default FileDbList;
