// ReplyBoard.tsx : 
import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import IReplyBoard from "../../types/IReplyBoard";
import ReplyBoardService from "../../services/ReplyBoardService";

function ReplyBoard(props: any) {
  // todo:  
  //    (bid, boardParent)
  const bid = props.bid;
  const boardParent = props.boardParent;
  //   
  let navigate = useNavigate();

  //  ( :  )
  const initialReplyBoard = {
    bid: "",
    boardTitle: "",
    boardContent: "",
    boardWriter: "",
    viewCnt: 0,
    boardGroup: null,
    boardParent: 0,
  };

  // 
  const [replyBoard, setReplyBoard] = useState<IReplyBoard>(initialReplyBoard);
  //      
  const [message, setMessage] = useState<string>("");

  // todo:  
  //  
  const getReplyBoard = (bid: string) => {
    ReplyBoardService.get(bid) //   
      .then((response: any) => {
        setReplyBoard(response.data);
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  //     + bid   
  useEffect(() => {
    if (bid) getReplyBoard(bid);
  }, [bid]);

  // input   
  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setReplyBoard({ ...replyBoard, [name]: value });
  };

  //  
  const updateReplyBoard = () => {
    ReplyBoardService.update(replyBoard.bid, replyBoard) //  
      .then((response: any) => {
        console.log(response.data);
        setMessage(" !");
        window.location.reload();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  //  :  
  const deleteReply = () => {
    ReplyBoardService.remove(replyBoard.bid) //  
      .then((response: any) => {
        console.log(response.data);
        alert(" .");
        window.location.reload();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  //  :  +  2  ( )
  //   (boardGroup) :  === 
  const deleteReplyBoard = () => {
    ReplyBoardService.removeBoard(replyBoard.boardGroup) //  
      .then((response: any) => {
        console.log(response.data);
        alert(".");
        window.location.reload();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  return (
    // 
    <>
      <>
        <h4></h4>

        {replyBoard ? (
          <div className="col-6 mx-auto">
            <form>
              <div className="row g-3 align-items-center mb-3">
                <div className="col-3">
                  <label htmlFor="boardTitle" className="col-form-label">
                    
                  </label>
                </div>

                <div className="col-9">
                  <input
                    type="text"
                    id="boardTitle"
                    required
                    className="form-control"
                    value={replyBoard.boardTitle}
                    onChange={handleInputChange}
                    placeholder="boardTitle"
                    name="boardTitle"
                    disabled={boardParent != "0" ? true : false}
                  />
                </div>
              </div>

              <div className="row g-3 align-items-center mb-3">
                <div className="col-3">
                  <label htmlFor="boardContent" className="col-form-label">
                    
                  </label>
                </div>

                <div className="col-9">
                  <input
                    type="text"
                    id="boardContent"
                    required
                    className="form-control"
                    value={replyBoard.boardContent}
                    onChange={handleInputChange}
                    placeholder="boardContent"
                    name="boardContent"
                  />
                </div>
              </div>

              <div className="row g-3 align-items-center mb-3">
                <div className="col-3">
                  <label htmlFor="boardWriter" className="col-form-label">
                    
                  </label>
                </div>

                <div className="col-9">
                  <input
                    type="text"
                    id="boardWriter"
                    required
                    className="form-control"
                    value={replyBoard.boardWriter}
                    onChange={handleInputChange}
                    placeholder="boardWriter"
                    name="boardWriter"
                  />
                </div>
              </div>
            </form>

            {/* boardParent "0"   */}
            <div className="row g-3 mt-3 mb-3">
              {boardParent != "0" ? (
                <button
                  className="btn btn-outline-primary ms-3 col"
                  onClick={deleteReply}
                >
                  
                </button>
              ) : (
                <button
                  className="btn btn-outline-danger ms-3 col"
                  onClick={deleteReplyBoard}
                >
                  
                </button>
              )}

              <button
                type="submit"
                className="btn btn-outline-success ms-2 col"
                onClick={updateReplyBoard}
              >
                
              </button>
            </div>

            {message && (
              <p className="alert alert-success mt-3 text-center">{message}</p>
            )}
          </div>
        ) : (
          <div className="col-6 mx-auto">
            <br />
            <p>Please click on a Reply Board...</p>
          </div>
        )}
      </>
    </>
  );
}

export default ReplyBoard;
