import React, { useState } from "react";

import IFileDb from "../../types/advanced/IFileDb";
import FileDbService from "../../services/advanced/FileDbService";

function AddFileDb() {
  // todo:  
  // todo:  
  const initialFileDb = {
    uuid: null, // (    )
    fileTitle: "", // 
    fileContent: "", // 
    fileUrl: "", //   URL
  };

  // uploadFileDb 
  const [uploadFileDb, setUploadFileDb] = useState<IFileDb>(initialFileDb);
  // todo  /   
  const [message, setMessage] = useState<String>("");
  //   todo:     
  const [selectedFiles, setSelectedFiles] = useState<FileList>();

  // todo :  
   // todo: input   
   const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target; // 
    setUploadFileDb({ ...uploadFileDb, [name]: value });  // 
  };

  //  
  const upload = () => {
    //    
    // ? = ,   null undefined 
    let currentFile = selectedFiles?.[0];   //   

    FileDbService.upload(uploadFileDb, currentFile)    //  
      .then((response: any) => {
        setMessage(" !");
        alert(".")
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // todo :      
  //   html  : <input type="file" />
  
  const selectFile = (event:any) => { 
    //      : event.target.files
    //  as  :     
    //                   (  )
    setSelectedFiles(event.target.files as FileList);
   }

  return (
    <>


      <div className="col-6 mx-auto">
        {/* <!-- (fileTitle)    --> */}
        <div className="row g-3 align-items-center mb-3">
          <div className="col-3">
            <label htmlFor="fileTitle" className="form-label">
              
            </label>
          </div>

          <div className="col-9">
            <input
              type="text"
              className="form-control"
              id="fileTitle"
              required
              name="fileTitle"
              value={uploadFileDb.fileTitle}
              onChange={handleInputChange}
            />
          </div>
        </div>
        {/* <!--     --> */}

        {/* <!--     --> */}
        <div className="row g-3 align-items-center mb-3">
          <div className="col-3">
            <label htmlFor="fileContent" className="form-label">
              
            </label>
          </div>

          <div className="col-9">
            <input
              type="text"
              className="form-control"
              id="fileContent"
              required
              name="fileContent"
              value={uploadFileDb.fileContent}
              onChange={handleInputChange}
            />
          </div>
        </div>
        {/* <!--     --> */}

        <div className="input-group mb-3">
          {/* upload / start */}
          <input
            type="file"
            className="form-control"
            id="inputGroupFile02"
            onChange={selectFile}
          />
          <button
            className="btn btn-outline-secondary"
            type="button"
            id="inputGroupFileAddon04"
            disabled={!selectedFiles}
            onClick={upload}
          >
            Upload
          </button>
        </div>
        {/* upload / end */}

        {/* upload /    */}
        {message && (
          <div className="alert alert-success" role="alert">
            {message}
          </div>
        )}
        {/* upload /    */}
      </div>
    </>
  );
}

export default AddFileDb;
