import React, { useEffect, useState } from 'react'
import Board from '../../types/Board';
import BoardService from '../../services/BoardService';
import { Link } from 'react-router-dom';
import { Pagination } from '@mui/material';

function BoardList(props:any) {
   //  
   const [board, setNotice] = useState<Array<Board>>([]);

   const [searchTitle, setSearchTitle] = useState<string>("");
 
   // todo   : page( ), count(  ), pageSize(3,6,9 )
   const [page, setPage] = useState<number>(1);
   const [count, setCount] = useState<number>(1);
   const [pageSize, setPageSize] = useState<number>(6); //   
   // todo  pageSizes :  (selectbox )
   const pageSizes = [3, 6, 9];
 
   //  
   useEffect(() => {
     retrieveBoard();
   }, [page, pageSize]);
 
   // todo)   
   const retrieveBoard = () => {
     //    : (page), 1 (pageSize)
     BoardService.getAll(searchTitle, page - 1, pageSize) //  
       .then((response: any) => {
         //   
         // es6(js)  :   
         //  
         // const dept =  response.data.dept;  // 
         // const totalPage = response.data.totalPages;  // 
         const { board, totalPages } = response.data;
         
         setNotice(board);
         setCount(totalPages);
         //  
         console.log("response", response.data);
       })
       .catch((e: Error) => {
         //   
         console.log(e);
       });
   };
 
   //    
   const onChangeSearchTitle = (e: any) => {
     const searchTitle = e.target.value;
     setSearchTitle(searchTitle);
   };
 
   // todo handlePageSizeChange() : pageSize    
   // select   :     
   const handlePageSizeChange = (e: any) => {
     setPageSize(e.target.value); // 1   (3, 6, 9)
     setPage(1); //   : 1 
   };
 
   // todo Pagination ()
   // page   => page   
   const handlePageChange = (e: any, value: number) => {
     // value ==  
     setPage(value);
   };
  return (
    <>
      <div className="d-grid gap-2 d-md-flex justify-content-md-end">
        <Link to="#">
          <button type="button" className="btn btn-outline-dark mb-3 " onClick={() => props.handleChangeBoard("addBoard")}>
            
          </button>
        </Link>
      </div>

      {/* table start( ) */}
      <div className="col-md-12">
        {/* table start */}
        <table className="table">
          <thead className="board-table text-white" style={{backgroundColor: "#bd5d38"}}>
            <tr>
              <th className="text-center col-md-1" scope="col">
                
              </th>
              <th className="text-center col-md-4" scope="col">
                
              </th>
              <th className="text-center col-md-1" scope="col">
                
              </th>
              <th className="text-center col-md-3" scope="col">
                
              </th>
            </tr>
          </thead>
          <tbody>
            {board &&
              board.map((data) => (
                <tr key={data.no}>
                  <td className="text-center">{data.no}</td>
                  <td className="text-center">
                  <Link to="#"><span onClick={()=>props.handleChangeBoard("boardDetail", data.no)}>{data.title}</span></Link>
                  </td>
                  <td className="text-center">{data.userName}</td>
                  <td className="text-center">{data.insertTime}</td>
                </tr>
              ))}
          </tbody>
        </table>
        {/* table end */}
      </div>
      {/* table end */}
      {/* dname start   */}
      <div className="row mb-5 justify-content-center">
        {/* w-50 :  , mx-auto : (margin: 0 auto), justify-content-center */}
        <div className="col-12 w-50 input-group mb-3">
          <input
            type="text"
            className="form-control"
            placeholder=" "
            value={searchTitle}
            onChange={onChangeSearchTitle}
          />
          <div className="input-group-append">
            <button
              className="btn btn-outline-secondary"
              type="button"
              onClick={retrieveBoard}
            >
              Search
            </button>
          </div>
        </div>
      </div>
      {/* dname end */}

      {/* paging (  )*/}
      <div className="mt-3  ">
        {"  : "}
        {/*  size  selectbox  */}
        <select onChange={handlePageSizeChange} value={pageSize}>
          {pageSizes.map((size) => (
            <option key={size} value={size}>
              {size + ""}
            </option>
          ))}
        </select>
        {/*  size  selectbox  */}

        {/* Material UI()   */}
        {/* count :   (), page :    */}
        {/* count={1}, page={} */}

        <Pagination
          className="pagination"
          count={count}
          page={page}
          siblingCount={1}
          boundaryCount={1}
          variant="outlined"
          shape="rounded"
          onChange={handlePageChange}
          color="primary"
        />

        {/* Material UI()   */}
      </div>
      {/* paging  */}
    </>
  )
}

export default BoardList