import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Board from "../../types/Board";
import BoardService from "../../services/BoardService";

function BoardDetail(props: any) {
  //    (dno)
  const no = props.no;
  //    
  let navigate = useNavigate();

  //  (  :  )
  const initialBoard = {
    no: "",
    title: "",
    userName: "",
    content: "",
    insertTime: "",
    viewCnt: 0,
  };

  //  
  const [board, setBoard] = useState<Board>(initialBoard);
  //      
  const [message, setMessage] = useState<string>("");

  const [viewBoard, setViewBoard] = useState<string>("");

  const [pid, setPid] = useState<number>(0);

  //     
  const handleChangeBoard = (viewBoard: string, pid = 0) => {
    setViewBoard(viewBoard); //  
    setPid(pid); //  
  };

  //   
  const getBoard = (no: string) => {
    BoardService.get(no) //   
      .then((response: any) => {
        setBoard(response.data);
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  //     + dno   
  useEffect(() => {
    if (no) getBoard(no);
  }, [no]);

  // input  
  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setBoard({ ...board, [name]: value });
  };

  // 
  const updateBoard = () => {
    BoardService.update(board.no, board) //  
      .then((response: any) => {
        alert(".");
        window.location.reload();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // 
  const deleteBoard = () => {
    BoardService.remove(board.no) //  
      .then((response: any) => {
        alert(".");
        window.location.reload();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };
  return (
    <>
  

          

      <table className="table  table-bordered">
        <tbody>
          <tr>
            <th className="col-md-2"></th>
            <td>{board.title}</td>
          </tr>
          <tr>
            <th className="col-md-2"></th>
            <td>{board.userName}</td>
          </tr>
          <tr>
            <th className="col-md-2"></th>
            <td>{board.insertTime}</td>
          </tr>
          <tr>
            <th className="col-md-2"></th>
            <td dangerouslySetInnerHTML={{ __html: board.content }}></td>
          </tr>
        </tbody>
      </table>
      <div className="d-grid gap-2 d-md-flex justify-content-md-center mb-3">
       
        <button
          type="button"
          className="btn btn-outline-secondary"
          data-bs-toggle="modal"
          data-bs-target="#updateModal"
        >
           
        </button>
        &nbsp;
        <button className="btn btn-outline-danger" onClick={deleteBoard}>
          
        </button>
      
        {/*   */}
        <div
          className="modal fade modal-lg"
          id="updateModal"
          tabIndex={-1}
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h1 className="modal-title fs-5" id="exampleModalLabel">
                   
                </h1>
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div className="modal-body">
                <label htmlFor="title" className="col-form-label">
                  
                </label>
                <input
                  type="text"
                  id="boardTitle"
                  required
                  className="form-control mb-3"
                  value={board.title}
                  onChange={handleInputChange}
                  placeholder=""
                  name="title"
                />
                <label htmlFor="userName" className="col-form-label ">
                  
                </label>
                <input
                  type="text"
                  id="userName"
                  required
                  className="form-control mb-4"
                  value={board.userName}
                  onChange={handleInputChange}
                  placeholder=""
                  name="userName"
                />
                <label htmlFor="content" className="col-form-label ">
                  
                </label>
                {/* TODO :    : CKeditor*/}
                <input
                  type="text"
                  id="content"
                  required
                  className="form-control mb-4"
                  value={board.content}
                  onChange={handleInputChange}
                  placeholder=""
                  name="content"
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-outline-secondary"
                  data-bs-dismiss="modal"
                >
                  
                </button>
                <button
                  type="button"
                  className="btn btn-outline-warning"
                  onClick={updateBoard}
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
        {/*    */}
      </div>
    </>
  );
}

export default BoardDetail;
