import React, { useEffect, useState } from "react";
import NoticeService from "../../services/NoticeService";
import { useNavigate, useParams } from "react-router-dom";
import Notice from "./../../types/Notice";
import axios from "axios";

function NoticeDetail(props: any) {
  //    (dno)
  const id = props.id;
  //    
  let navigate = useNavigate();

  //  (  :  )
  const initialNotice = {
    id: "",
    title: "",
    userName: "",
    content: "",
    insertTime: "",
  };

  //  
  const [notice, setNotice] = useState<Notice>(initialNotice);
  //      
  const [message, setMessage] = useState<string>("");

  const [viewNotice, setViewNotice] = useState<string>("");

  const [pid, setPid] = useState<number>(0);

  //     
  const handleChangeNotice = (viewNotice: string, pid = 0) => {
    setViewNotice(viewNotice); //  
    setPid(pid); //  
  };

  //   
  const getNotice = (id: string) => {
    NoticeService.get(id) //   
      .then((response: any) => {
        setNotice(response.data);
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  //     + dno   
  useEffect(() => {
    if (id) getNotice(id);
  }, [id]);

  // input  
  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setNotice({ ...notice, [name]: value });
  };

  // 
  const updateNotice = () => {
    NoticeService.update(notice.id, notice) //  
      .then((response: any) => {
        alert(".");
        window.location.reload();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // 
  const deleteNotice = () => {
    NoticeService.remove(notice.id) //  
      .then((response: any) => {
        alert(".");
        window.location.reload();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  return (
    <>
      <div className="d-grid gap-2 d-md-flex justify-content-md-start mb-3">
        <button
          type="button"
          className="btn btn-outline-secondary"
          data-bs-toggle="modal"
          data-bs-target="#updateModal"
        >
           
        </button>

        <button className="btn btn-outline-danger" onClick={deleteNotice}>
                  
                </button>
        {/*   */}
        <div
          className="modal fade modal-lg"
          id="updateModal"
          tabIndex={-1}
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h1 className="modal-title fs-5" id="exampleModalLabel">
                   
                </h1>
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div className="modal-body">
                <label htmlFor="title" className="col-form-label">
                  
                </label>
                <input
                  type="text"
                  id="boardTitle"
                  required
                  className="form-control mb-3"
                  value={notice.title}
                  onChange={handleInputChange}
                  placeholder=""
                  name="title"
                />
                <label htmlFor="userName" className="col-form-label ">
                  
                </label>
                <input
                  type="text"
                  id="userName"
                  required
                  className="form-control mb-4"
                  value={notice.userName}
                  onChange={handleInputChange}
                  placeholder=""
                  name="userName"
                />
                <label htmlFor="content" className="col-form-label ">
                  
                </label>
                {/* TODO :    : CKeditor*/}
                <input
                  type="text"
                  id="content"
                  required
                  className="form-control mb-4"
                  value={notice.content}
                  onChange={handleInputChange}
                  placeholder=""
                  name="content"
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-outline-secondary"
                  data-bs-dismiss="modal"
                >
                  
                </button>
                <button
                  type="button"
                  className="btn btn-outline-warning"
                  onClick={updateNotice}
                >
                  
                </button>

                
              </div>
            </div>
          </div>
        </div>
        {/*    */}
      </div>

      <table className="table">
        <tbody>
          <tr>
            <th className="col-md-2"></th>
            <td>{notice.title}</td>
          </tr>
          <tr>
            <th className="col-md-2"></th>
            <td>{notice.userName}</td>
          </tr>
          <tr>
            <th className="col-md-2"></th>
            <td>{notice.insertTime}</td>
          </tr>
        </tbody>
      </table>
      <div dangerouslySetInnerHTML={{ __html: notice.content }}></div>

    
    </>
  );
}

export default NoticeDetail;
